import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './components/App';
import * as serviceWorker from './services/serviceWorkers/serviceWorker';
import Home from './scenes/Home';
import SubCategories from './scenes/SubCategories';
var routes = [
    {
        path: '/academix',
        exact: true,
        component: Home,
    },
    {
        path: '/academix/:categoryId/:categoryName',
        exact: false,
        component: SubCategories,
    },
];
ReactDOM.render(React.createElement(App, { routes: routes }), document.getElementById('root'));
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
